.carousel-control-prev,
.carousel-control-next {
    width: 30%;
    opacity: 1;
}

.carousel-caption {
    bottom: 30%;
}

.carousel-text {
    font-weight: bold;
    font-size: 32px;
    line-height: 37px;

    @include media-breakpoint-up(md) {
        font-weight: 800;
        font-size: 48px;
        line-height: 56px;
    }

}

//searchBar
.searchBar-position{
    margin-top: -56px;
    border-radius: 8px;

    @include media-breakpoint-up(lg) {
        margin-top: -50px;
    }

}
.searchBar-content {
    
    .input-group-text {
        padding: 1rem 1rem;
        font-size: 1.5rem;
        font-weight: 400;
        line-height: 1;
        color: #000;
        background-color: #FFFFFF;
        border: 0;
        border-radius: 4px 0 0 4px;
    }

    .searchBar-input {
        width: 100%;
        height: calc(1.5em + 2rem + 4x);
        padding: 1rem 1rem 1rem 0;
        font-size: 1rem;
        font-weight: 400;
        line-height: 22px;
        color: rgba(0, 0, 0, 0.6);
        border: 0;
        background: #FFFFFF 0% 0% no-repeat padding-box;
        border-radius: 0px 4px 4px 0px;
        ;

        &:focus {
            outline: 0;
        }
    }

    .input-group>.searchBar-input {
        position: relative;
        -webkit-box-flex: 1;
        -ms-flex: 1 1 0%;
        flex: 1 1 0%;
        min-width: 0;
        margin-bottom: 0;
    }

    .searchBar-btn {
        padding: 17px 32px;
    }
}
.searchBar-item{
    .btn-dropdown{
        padding: 1rem 1rem ;
    }
    .dropdown-item:hover,
    .dropdown-item:focus {
        background-color: $primary-light;
    }
}

//top
.top {
    a {
        color: rgba(0, 0, 0, 0.6);
    }

    .nav-tabs .nav-link {
        font-size: 20px;
        line-height: 24px;
        font-weight: bold;
        border: 0;
        border-bottom: 1px solid #F1F0F8;

        &:hover {
            border-bottom: 2px solid $primary;
        }

        @include media-breakpoint-up(md) {
            font-size: 24px;
            line-height: 29px;
        }

    }

    .nav-link {
        padding: 0 1.5rem;
        
    }

    .nav-tabs .nav-link.active,
    .nav-tabs .nav-item.show .nav-link {
        color: $primary;
        background-color: #fff;
        border-bottom: 2px solid $primary;
    }

}

//destination
.destination {
    .card {
        border-radius: 8px;
        border: 0;
    }

    .card-img {
        height: 78px;
        background-position: center;
        background-size: cover;
        object-fit: cover;
        // filter: grayscale(50%);
    }

    .card-img-overlay {
        padding: 1rem 1.5rem;
        background: transparent linear-gradient(270deg, #00000000 0%, #000000 100%) 0% 0% no-repeat padding-box;
    }

    .card-title {
        line-height: 17px;
        margin-bottom: 4px;
    }

    .card-subtitle {
        margin-bottom: 0;
    }
}

.osakaImg {
    background-image: url(../images/osaka.jpg);
}

//inspiration
.ins {
    .card-img-top {
        height: 181px;
        margin-bottom: 20px;

        @include media-breakpoint-up(md) {
            height: 158px;
            margin-bottom: 24px;
        }

        @include media-breakpoint-up(lg) {
            height: 263px;
        }
    }
    .card-body{
        padding: 0;
    }
}