//result

.resultSearch{
    .searchBar-item {
        .btn-dropdown {
            padding: .5rem 1rem;
        }
    }
}
.result {
    .page {
        border-radius: 8px;
    }
}

.arrangement {
    li.active {
        color: $primary;
    }
}

.filter-item {
    padding: 1.5rem 0;
    border-bottom: 2px solid #fff;

    &:nth-child(5) {
        border: 0;
    }

    //checkbox
    .custom-control {
        min-height: 1.5rem;
        padding-left: 1.5rem;
        margin-bottom: 8px;
    }

    .custom-control-input {
        width: 1.5rem;
        height: 1.5rem;

    }

    .custom-control-label {
        margin-left: 4px;
    }

    .custom-control-label::before {
        position: absolute;
        top: 0rem;
        left: -1.75rem;
        width: 1.5rem;
        height: 1.5rem;
        content: "";
        background-color: #fff;
        border: #000 solid 2px;
    }

    .material-icons {
        font-size: 18px;
        vertical-align: sub;
    }
}

.data {
    .item-img {
        width: 255px;
        height: 175px;
        border-radius: 8px 0px 0px 8px;
        object-fit: cover;
    }

    .data-item {
        cursor: pointer;

        .item-content {

            border: 1px solid rgba(241, 240, 248, 0);
        }

        &:hover {
            .item-content {
                border-right: 1px solid $light;
                border-bottom: 1px solid $light;
                box-shadow: 4px 4px $light;
            }
        }
    }

    .item-divider {
        width: 100%;
        height: 1px;
        background: $primary-light;
        margin: 24px 0;
    }

}