//
sub {
    bottom: 0.5em;
}
.h7 {
    font-size: 14px;
}

.h8 {
    font-size: 12px;
}

.font-weight-extrabold {
    font-weight: 800;
}

.font-raleway {
    font-family: Raleway;
}

.font-opensans {
    font-family: Open Sans;
}

.font-roboto {
    font-family: Roboto;
}
.bg-cover {
    background-size: cover;
    background-position: center center;
}


//layout
.header {
    .nav-link {
        color: #121212;

        &:hover {
            color: $primary;
        }
    }

    .navbar-toggler {
        color: #000;

        &:focus {
            outline: none;
        }
    }
}

.footer {
    line-height: 19px;

    .dropdownBtn {
        display: flex;
        justify-content: center;
        align-items: center;
        text-decoration: none;
    }
}

.subscribe {
    padding: 24px 0;

    @include media-breakpoint-up(md) {
        padding: 92px 0;
    }

    .subscribeTitle {
        font-size: 24px;
        line-height: 29px;
        font-weight: bold;

        @include media-breakpoint-up(md) {
            font-size: 48px;
            line-height: 56px;
            font-weight: 800;
        }
    }

    .subscribeInput {
        height: auto;
        padding: 16px 21px;
        line-height: 1.5;
        border: 0;
        border-radius: 4px 0px 0px 4px;
    }
    .subscribeBtn{
        padding: 16px 16px;
        border-radius: 0px 4px 4px 0px;
        @include media-breakpoint-up(md) {
            padding: 16px 32px;
        }
    }


}