html,
body {
    position: relative;
    height: 100%;
}


.swiper-container {
    width: 100%;
    // height: 100%;
}


.swiper-slide {
    text-align: left;
    background: #fff;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.swiper-slide-item {
    border: 0;

    .card-img-top {
        height: 164px;
        border-radius: 8px;
        margin-bottom: 8px;

        @include media-breakpoint-up(md) {
            height: 185px;
        }

        @include media-breakpoint-up(lg) {
            height: 175px;
        }
    }

    .card-body {
        padding: 0;
    }

    .card-title {
        line-height: 22px;
    }

    .material-icons {
        font-size: 18px;
    }
}

.swiper-btn{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #F1F0F8;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transform: translateY(-50%);

}
